import "./App.css"
import React, {useState, useEffect } from 'react'

import Chart from "chart.js/auto";
import { Line } from "react-chartjs-2";

function Supply(props){
  const {supply} = props
  return(
    <tr>
      <td>{supply.id}</td>
      <td>{supply.order_id}</td>
      <td>{supply.price_usd}</td>
      <td>{supply.price_ru}</td>
      <td>{supply.date_supply}</td>
    </tr>
  )
}

function App() {
  function getSum(total, num) {
    return total + Math.round(num);
  }
  const [supplies, setSupplies] = useState([])
  const total = supplies.map(function(item){return item.price_usd;}).reduce(getSum, 0)

  const data = {
    labels: supplies.map(function(item) {
        return item.date_supply;
      }),
    datasets: [
      {
        label: "",
        backgroundColor: "rgb(255, 99, 132)",
        borderColor: "rgb(255, 99, 132)",
        data: supplies.map(function(item) {
          return item.price_usd;
        })
      },
    ],
  };
  
  useEffect (() => {
    async function fetchSupplies() {
      const res = await fetch("http://127.0.0.1:8000/supply_lists/");
      res.json().then(data =>  setSupplies(data.data))
    }
    fetchSupplies();
  },[])

  return (
    <div>
      <header>
        <h1>Supply List</h1>
      </header>
      <div className="supply-list">
        <div className="charts">
          <Line data={data} />
        </div>
        <div>
          <div className="total">
            <h1>
              Total
            </h1>
            <p>
              {total}
            </p>
          </div>
          <div className='div-table'>
            <table>
              <thead>
                <tr>
                  <th>№</th>
                  <th>заказ №</th>
                  <th>стоимость,$</th>
                  <th>стоимость,₽</th>
                  <th>срок поставки</th>
                </tr>
              </thead>
              <tbody>
                {supplies.map((item, index) =>
                  <Supply supply={item}></Supply>
                )}
              </tbody>
              <tfoot>
              </tfoot>
            </table>
          </div>
        </div>
      </div>
    </div>
  );
}

export default App